<template>
    <div class="api">
        <h2>WebStorage API</h2>
        <div class="api-info">
            <h3>LocalStorage</h3>
            <p><strong>localStorage</strong>—This mechanism works similar to a storage system for a desktop application. The data saved is permanently preserved and always available from the application that created it.
            </p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>setItem(key, value)</td>
                <td>This is the method we have to call to create an item.</td>
            </tr>
            <tr>
                <td>getItem(key)</td>
                <td>This is the method we have to call to create an item.</td>
            </tr>
            <tr>
                <td>setItem(key, value)</td>
                <td>To retrieve the value of an item, we must call this method by specifying the keyword of the item we want. The keyword in this case is the same as the keyword declared when the item was created by setItem().</td>
            </tr>
            <tr>
                <td>removeItem(key)</td>
                <td>Delete one single item.</td>
            </tr>
            <tr>
                <td>clear()</td>
                <td>This method will simply empty the storage space.</td>
            </tr>
            <tr>
                <td>key(index)</td>
                <td>The items are stored sequentially, enumerated with an automatic index that starts from 0. With this method, we can retrieve a specific item or create a loop to retrieve all the information stored. Get the name of a key at a particular index using sessionStorage.key(0)</td>
            </tr>
        </table>
        <pre class="api-code">
	localStorage.setItem('foo', 'bar'); 
	localStorage.setItem('foo2', 'bar2'); 
	localStorage.getItem('foo'); // bar 
	localStorage.removeItem('foo'); 
	localStorage.key(0) // foo2 
	localStorage.clear(); // clear storage 
        </pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>length</td>
                <td>Get the number of item(key=>value) stored in local storage.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>length</td>
                <td>Get the number of item(key=>value) stored in local storage.</td>
            </tr>
        </table>
        <pre class="api-code">
	localStorage.length // the number of items, example 3 
        </pre>
        <h4 class="api-table-caption">The Events</h4>
        <table class="api-table">
            <tr>
                <td>storage</td>
                <td>The storage event is fired when a storage area changes window.addEventListener('storage', onStorage, false);</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>The Attributes</h3>
        </div>
        <div class="api-info">
            <h3>sessionStorage</h3>
            <p><strong>sessionStorage</strong>—This is a storage mechanism that will keep data available only for the duration of a page session. The information stored through this mechanism is only accessible from a single window or tab and it lasts until the window is closed.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>setItem(key, value)</td>
                <td>This is the method we have to call to create an item.</td>
            </tr>
            <tr>
                <td>getItem(key)</td>
                <td>To retrieve the value of an item, we must call this method by specifying the keyword of the item we want. The keyword in this case is the same as the keyword declared when the item was created by setItem().</td>
            </tr>
            <tr>
                <td>removeItem(key)</td>
                <td>This method will delete one single item.</td>
            </tr>
            <tr>
                <td>clear()</td>
                <td>This method will simply empty the storage space.</td>
            </tr>
            <tr>
                <td>key(index)</td>
                <td>The items are stored sequentially, enumerated with an automatic index that starts from 0. With this method, we can retrieve a specific item or create a loop to retrieve all the information stored. Get the name of a key at a particular index using sessionStorage.key(0)</td>
            </tr>
        </table>
        <pre class="api-code">
	sessionStorage.setItem('foo', 'bar'); 
	sessionStorage.setItem('foo2', 'bar2'); 
	sessionStorage.getItem('foo'); // bar 
	sessionStorage.removeItem('foo'); 
	sessionStorage.key(0) // foo2 
	sessionStorage.clear(); // clear storage
		</pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table>
            <tr>
                <td>length</td>
                <td>the number of items accumulated in the storage space for this application. It works exactly like the length property used regularly in Javascript for arrays, and it is useful for sequential readings. Get the number of key/value pairs stored in local storage with sessionStorage.length</td>
            </tr>
        </table>
        <pre class="api-code">
	sessionStorage.length // the number of items, example 3 
		</pre>
        <h4 class="api-table-caption">The Events</h4>
        <table>
            <tr>
                <td>storage</td>
                <td>The storage event is fired when a storage area changes</td>
            </tr>
        </table>
        <pre class="api-code">
	window.addEventListener('storage', onStorage, false);
		</pre>
        <div class="api-info">
            <h3>The Attributes</h3>
        </div>
    </div>
</template>
<script>
export default {
    name: 'WebStorage',
    data() {
        return {}
    }
}
</script>



